#Welcome{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#Welcome > p.interactionHint{
  position: absolute;
  color: white;
  top: 4rem;
  animation: Breath 4s linear infinite, TitleFlowDown 3s forwards;;
}

#BasicInfo{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  bottom: 1rem;
  color: white;
  z-index: 2;
  animation: FadeIn 3s forwards;
}

#BasicInfo > .foldBtn{
  display: none;
}

#BasicInfo > h2{
  text-align: center;
  margin: 0.5rem 1.5rem;
  line-height: 1.75;
}

#BasicInfo > p{
  width: calc(100% - 3rem);
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.5rem 1.5rem;
  font-size: 1.25rem;
}

@media screen and (max-width: 767px){
  #BasicInfo{
    background: rgba(255, 255, 255, 1);
    width: 100%;
    border-radius: 2rem 2rem 0 0;
    padding-bottom: 1rem;
    top: 3rem;
    bottom: 0;
    overflow: hidden;
    justify-content: space-between;
    color: #444444;
    transition: all 0.3s 0.2s linear;
  }

  #BasicInfo.folded{
    background: rgba(255, 255, 255, 0);
    top: calc(100% - 3rem);
    bottom: -160vh;
    border-radius: 2rem 2rem 0 0;
    transition: all 0.4s ease-in-out;
  }

  #BasicInfo > h2 > .auxiliaryBr{
    display: block;
  }

  #BasicInfo > .foldBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 3rem;
    border: none;
    background: none;
  }

  #BasicInfo > .foldBtn > svg{
    color: #444444;
    font-size: 1.5rem;
    transition: all 0.4s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  }
  #BasicInfo.folded > .foldBtn > svg{
    color: white;
    transform: rotate(-180deg);
    transition: all 0.4s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  }

  #BasicInfo > p{
    flex-direction: column;
  }
  #BasicInfo > p > span{
    margin: 0.5rem 0;
  }
}

@keyframes Breath {
  50%{
    transform: scale(1.2);
  }
}

@keyframes FadeIn {
  0%{
    opacity: 0;
  }
}

@keyframes TitleFlowDown {
  0%{
    transform: translateY(-20vh);
    opacity: 0;
  }
}