#BluredBackground{
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: linear-gradient(to bottom right,var(--cyan), var(--purple));
  z-index: -10;
  overflow: hidden;
}

.bluredBall{
  position: absolute;
  border-radius: 50%;
  filter: blur(200px);
}

.bluredBall:nth-child(1){
  width: 35vw;
  height: 35vw;
  top: 5vh;
  right: 40vw;
  background: #7abcf1;
  animation: RandomMove 8s infinite;
}

.bluredBall:nth-child(2){
  width: 30vw;
  height: 30vw;
  bottom: 15vh;
  right: 20vw;
  background: #b48fe1;
  animation: RandomMove 10s 2s infinite;
}

.bluredBall:nth-child(3){
  width: 25vw;
  height: 25vw;
  top: 20vh;
  left: 15vw;
  background: #dc7fba;
  animation: RandomMove 6s 5s infinite;
}

@keyframes RandomMove {
  20%{
    transform: translate(20%, 20%);
  }
  40%{
    transform: translate(-10%, 60%);
  }
  60%{
    transform: translate(-70%, 0%);
  }
  80%{
    transform: translate(10%, -30%);
  }
}

@media screen and (max-width: 767px) {
  .bluredBall{
    filter: blur(140px);
  }
  .bluredBall:nth-child(1){
    width: 35vh;
    height: 35vh;
  }
  .bluredBall:nth-child(2){
    width: 30vh;
    height: 30vh;
  }
  .bluredBall:nth-child(3){
    width: 25vh;
    height: 25vh;
  }
}