.sideMenuBtn{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.75rem;
  height: 2.75rem;
  cursor: pointer;
}



.sideMenuBtn > .dot{
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 2px;
  background: var(--textBlack);
  transition: all 0.2s ease-out;
}

.sideMenuBtn > .dot:nth-child(1){
  transform: translate(-10px, -6px);
}

.sideMenuBtn > .dot:nth-child(2){
  transform: translate(-10px, 6px);
}

.sideMenuBtn > .bar{
  position: absolute;
  width: 16px;
  height: 3px;
  border-radius: 2px;
  background: var(--textBlack);
  transition: all 0.2s ease-out;
}

.sideMenuBtn > .bar:nth-child(3){
  transform: translate(4px, -6px);
}

.sideMenuBtn > .bar:nth-child(4){
  transform: translate(4px, 6px);
}

.sideMenuBtn.activated > .dot{
  width: 12px;
  height: 3px;
  transition: all 0.2s ease-out;
}
.sideMenuBtn.activated > .dot:nth-child(1){
  transform: translate(-30%, -100%) rotate(45deg);
}
.sideMenuBtn.activated > .dot:nth-child(2){
  transform: translate(-30%, 100%) rotate(-45deg);
}

.sideMenuBtn.activated > .bar{
  width: 12px;
  height: 3px;
  transition: all 0.2s ease-out;
}
.sideMenuBtn.activated > .bar:nth-child(3){
  transform: translate(30%, -100%) rotate(-45deg) ;
}
.sideMenuBtn.activated > .bar:nth-child(4){
  transform: translate(30%, 100%) rotate(45deg);
}

@media screen and (min-width: 768px) {
  .sideMenuBtn::after{
    content: "";
    position: absolute;
    width: 0%;
    height: 100%;
    opacity: 0;
    background: var(--purple);
    z-index: -1;
    transition: all 0.2s ease-out;
  }
  
  .sideMenuBtn:hover::after{
    width: 100%;
    opacity: 0.3;
    transition: all 0.2s ease-out;
  }

  .sideMenuBtn:hover > .dot, .sideMenuBtn:hover > .bar{
    background-color: var(--purple);
  }
}