*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  --pale: rgba(240, 240, 240, 1);
  --lightGrey: #dddddd;
  --darkGrey: #bbbbbb;
  --textBlack: #444444;
  --textGrey: #999999;
  --shadow: rgba(50, 50, 50, 0.2);
  --cyan: #d1f0ee;
  --purple: #7d88be;
  --darkPurple: #8381b5;
}

#App::after{
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -10;
  background: var(--pale);
}

p.interactionHint{
  color: #aaaaaa;
  font-size: 0.875rem;
}

.routePage{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: fit-content;
  overflow-x: hidden;
  z-index: 0;
}

/*路由切换动画*/
.route-exit-active{
  opacity: 0;
  transition: opacity 0.2s linear;
}

.route-enter{
  opacity: 0;
}

.route-enter-active{
  opacity: 1;
  transition: opacity 0.6s 0.4s ease-in;
}