#SideMenu{
  position: fixed;
  top: 4rem;
  right: 0;
  width: min(calc(50% - 520px), 400px);
  list-style: none;
  z-index: 9;
  transition: all 0.4s ease-out;
}

#SideMenu.hidden{
  transform: translateX(120%);
  transition: all 0.4s ease-out;
}

#SideMenu > .menuTitle{
  display: block;
  margin-bottom: 1rem;
  font-weight: 600;
  color: var(--textBlack);
}

#SideMenu > li{
  display: flex;
  align-items: center;
  height: 2rem;
  padding: 0 1rem;
  border-left: 3px solid var(--lightGrey);
  color: var(--textBlack);
  font-size: 0.875rem;
  font-weight: 400;
  cursor: pointer;
}

#SideMenu > li:hover{
  font-weight: 800;
}

#SideMenu > li.current{
  border-left: 3px solid var(--purple);
  font-weight: 800;
}

@media screen and (max-width: 1023px) {
  #SideMenu{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: min(100%, 400px);
    height: 100vh;
    top: 0;
    background: var(--pale);
  }

  #SideMenu.hidden{
    transform: translateX(100%);
  }

  #SideMenu > .menuTitle {
    display: none;
  }

  #SideMenu > li{
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% + 16px);
    font-size: 1.25rem;
    height: 4rem;
    display: flex;
    border-left: 8px solid var(--lightGrey);
  }

  #SideMenu > li.current{
    border-left: 8px solid var(--purple);
  }

}