.ARImage{
  position: relative;
  width: 110%;
  height: fit-content;
  overflow: hidden;
  box-shadow: 0px 0px 16px var(--shadow);
}

.ARImage > img{
  width: 100%;
}

.ARImage > img.processedImg{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.25, 0.9, 0.68,1);
}

.ARImage.ARShow > img.processedImg{
  opacity: 1;
  transition: opacity 1s cubic-bezier(0.25, 0.9, 0.68,1);
}

.ARImage > .imgControl{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 3rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}

.ARImage > .imgControl > img{
  width: 2rem;
  height: 2rem;
  margin: 0 1rem;
  cursor: pointer;
  transition: all 0.3s linear;
}

.ARImage > .imgControl > img:hover{
  transform: scale(1.3);
  transition: all 0.2s linear;
}

.ARImage > .exampleInfo{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: min(100%, 400px);
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100%);
  z-index: 2;
  background: rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease-out;
}

.ARImage > .exampleInfo.infoShow{
  transform: translateX(0);
  transition: all 0.3s ease-out;
}

.ARImage > .exampleInfo > span{
  width: 80%;
  margin: 0.5rem 0;
  color: white;
  line-height: 2;
}

.ARImage > .exampleInfo > span > em{
  font-style: normal;
  font-weight: 800;
}

@media screen and (max-width: 1023px) {
  .ARImage{
    width: 100%;
    box-shadow: none;
  }
}