#TitleEntrance{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: min(60vh, 90vw);
  height: min(60vh, 90vw);
  border-radius: 50%;
  transform: translateY(-5%);
  animation: TitleFlowDown 3s forwards;
  cursor: pointer;
}

#TitleEntrance > h1{
  text-align: center;
  font-size: min(12vh, 18vw);
  color: white;
}

#TitleEntrance > h1:nth-child(1){
  transition: opacity 1s 0.5s linear;
}

#TitleEntrance > h1:nth-child(2){
  position: absolute;
  opacity: 0;
  transition: opacity 1s linear;
}

#TitleEntrance > .titleRing{
  position: absolute;
  width: 80%;
  height: 80%;
  border-radius: 50%;
  border: solid 1px white;
  transition: all 1s ease-out;
}

#TitleEntrance > .titleRing:nth-child(3){
  left: 0%;
  top: 0%;
  transform-origin: 55% 60%;
  animation: RingRoll 8s linear infinite;
}
#TitleEntrance > .titleRing:nth-child(4){
  left: 20%;
  top: 10%;
  transform-origin: 40% 55%;
  animation: RingRoll 7s linear infinite reverse;
}
#TitleEntrance > .titleRing:nth-child(5){
  left: 10%;
  top: 20%;
  transform-origin: 45% 40%;
  animation: RingRoll 6s linear infinite;
}

#TitleEntrance:hover > h1:nth-child(1){
  opacity: 0;
  transition: opacity 1s linear;
}

#TitleEntrance:hover > h1:nth-child(2){
  opacity: 1;
  transition: opacity 1s 0.5s linear;
}

#TitleEntrance:hover > .titleRing{
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  animation-play-state: paused;
  transform: rotate(0deg) scale(1);
  transform-origin: 50% 50%;
  transition: all 1s ease-out;
}

@keyframes RingRoll {
  25%{
    transform: rotate(90deg);
  }
  50%{
    transform: rotate(180deg);
  }
  75%{
    transform: rotate(270deg);
  }
  100%{
    transform: rotate(360deg);
  }
}