#NavBar{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 2.75rem;
  position: fixed;
  top: 0;
  left: 0;
  background: var(--pale);
  box-shadow: 0px 0px 8px rgba(240, 240, 240, 0.9);
  z-index: 10;
}

#NavBar > .textItems{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: min(calc(100% - 6rem), 900px);
  height: 100%;
  position: relative;
}

#NavBar > .textItems > a{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  height: 100%;
  font-size: 1rem;
  font-weight: 800;
  color: var(--textBlack);
}

#NavBar > .textItems > a:hover, #NavBar > .iconItems > .iconLink:hover > svg{
  color: var(--purple);
}

#NavBar > .textItems > a::before, #NavBar > .textItems > a::after, #NavBar > .iconItems > .iconLink::after{
  content: "";
  position: absolute;
  width: 0%;
  height: 100%;
  opacity: 0;
  transition: all 0.2s ease-out;
}

#NavBar > .textItems > a::before{
  border-bottom: 2px solid var(--purple);
  z-index: 1;
}

#NavBar > .textItems > a::after, #NavBar > .iconItems > .iconLink::after{
  background: var(--purple);
  z-index: -1;
}

#NavBar > .textItems > a.active{
  color: var(--purple);
}

#NavBar > .textItems > a.active::before{
  width: 100%;
  opacity: 1;
  transition: all 0.2s ease-out;
}

#NavBar > .textItems > a:hover::after, #NavBar > .iconItems > .iconLink:hover::after{
  width: 100%;
  opacity: 0.3;
  transition: all 0.2s ease-out;
}

#NavBar > .sideMenuBtn{
  position: absolute;
  right: 0;
}

#NavBar > .iconItems{
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.75rem;
  height: 2.75rem;
}

#NavBar > .iconItems > .iconLink{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

#NavBar > .iconItems > .iconLink > svg{
  color: var(--textBlack);
}

#NavBar > .iconItems > .iconLink:nth-child(2){
  display: none;
}