.systemAnalysis{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 110%;
  height: fit-content;
  position: relative;
  margin: 1rem 0;
  box-shadow: 0px 0px 16px var(--shadow);
  cursor: pointer;
}

.systemAnalysis::before, .systemAnalysis::after{
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  z-index: -1;
  transition: all 0.4s linear;
}

.systemAnalysis::before{
  top: 0;
  left: 0;
  border-top: 8px solid var(--darkPurple);
  border-left: 8px solid var(--darkPurple);
}

.systemAnalysis::after{
  bottom: 0;
  right: 0;
  border-bottom: 8px solid var(--darkPurple);
  border-right: 8px solid var(--darkPurple);
}

.systemAnalysis:hover::before, .systemAnalysis:hover::after{
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 10;
  transition: all 0.4s linear;
}

.systemAnalysis.stateOver{
  animation: StateOver 0.5s linear;
}

.systemAnalysis > .analysisImg{
  width: 100%;
  position: absolute;
}

.systemAnalysis > .analysisImg:nth-child(1){
  position: relative;
}

.systemAnalysis > .analysisImg.show{
  opacity: 1;
  transform: translateY(0);
  transition: all 0.6s ease-out;
}

.systemAnalysis > .analysisImg.hidden{
  opacity: 0;
  transform: translateY(-2rem);
}

.systemAnalysis > .analysisText{
  position: absolute;
  margin-top: 1rem;
  font-weight: 600;
  color: var(--textGrey);
}

@keyframes StateOver {
  10%{
    transform: rotate(1deg);
  }
  20%{
    transform: rotate(-1deg);
  }
  40%{
    transform: rotate(2deg);
  }
  60%{
    transform: rotate(-2deg);
  }
  80%{
    transform: rotate(1deg);
  }
  90%{
    transform: rotate(-1deg);
  }
}

@media screen and (max-width: 1023px) {
  .systemAnalysis{
    width: 100%;
    box-shadow: none;
  }
}