#NavBarLite{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: calc(80px * var(--ratio));
  padding-left: 1.5rem;
  padding-right: 1rem;  
  position: fixed;
  top: 0;
  left: 0;
  background: var(--pale);
  z-index: 10;
  transition: all 0.2s linear;
}

#NavBarLite > .pageTitles{
  display: flex;
  align-items: center;
  width: calc(100% - 6rem);
}

#NavBarLite > .pageTitles > h2{
  display: none;
  color: var(--textBlack);
  font-size: calc(40px * var(--ratio));
  margin-right: 1.5rem;
}
#NavBarLite > .pageTitles > h2.current, #NavBarLite > .pageTitles > .foldCtrl{
  display: inline-block;
  font-size: calc(40px * var(--ratio));
  color: var(--textBlack);
}

#NavBarLite > .pageTitles > .foldCtrl{
  display: flex;
  align-items: center;
  justify-content: center;
}

#NavBarLite > .pageTitles > .foldCtrl > svg{
  font-size: calc(32px * var(--ratio));
  transition: all 0.2s linear;
}
#NavBarLite.folded > .pageTitles > .foldCtrl > svg{
  transform: rotate(-180deg);
  transition: all 0.2s linear;
}

#NavBarLite > .otherPages{
  position: absolute;
  top: calc(80px * var(--ratio));
  left: 0;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 100%;
  height: 2rem;
  background: var(--pale);
  border-bottom: 1px solid var(--lightGrey);
  padding: 0 1.5rem;
  overflow: hidden;
  transition: top 0.2s linear, height 0.3s linear;
}

#NavBarLite.folded > .otherPages{
  border-bottom: none;
  height: 0;
  transition: top 0.2s linear, height 0.3s linear;
}

#NavBarLite > .otherPages > a{
  color: var(--purple);
}