.imageWithName {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: 1rem 0;
}

.imageWithName > .imgWrapper{
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 110%;
  height: fit-content;
  box-shadow: 0px 0px 16px var(--shadow);
}

.imgWrapper > img{
  width: 100%;
  transition: all 0.3s linear;
}

.imgWrapper::before, .imgWrapper::after{
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  z-index: -1;
  transition: all 0.4s linear;
}

.imgWrapper::before{
  top: 0;
  left: 0;
  border-top: 8px solid var(--darkPurple);
  border-left: 8px solid var(--darkPurple);
}

.imgWrapper::after{
  bottom: 0;
  right: 0;
  border-bottom: 8px solid var(--darkPurple);
  border-right: 8px solid var(--darkPurple);
}

.imgWrapper:hover::before, .imgWrapper:hover::after{
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1;
  transition: all 0.4s linear;
}

.imageWithName > span.imgName{
  margin-top: 1rem ;
  color: var(--textGrey);
  font-size: 0.875rem;
}

@media screen and (max-width: 1023px) {
  .imageWithName > .imgWrapper{
    width: 100%;
    box-shadow: none;
  }
}