/******通用样式********/
#Content{
  display: flex;
  justify-content: center;
  background: var(--pale);
}

.contentPage{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: min(calc(100% - 6rem), 900px);
  background: white;
  box-shadow: 0px 12px 16px var(--shadow);
  margin-top: 8rem;
  margin-bottom: 2rem;
  padding: 1.5rem 0;
  animation: FlowUp 2s ease-out forwards;
}

.contentPage .contentSection{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.contentPage .contentSection h3{
  width: calc(100% - 4rem);
  margin: 0.5rem 0;
  font-size: 1.75rem;
  color: var(--purple);
}

.contentPage p.contentText{
  width: calc(100% - 4rem);
  margin: 0.25rem 0;
  line-height: 2;
  color: var(--textBlack);
}

@keyframes FlowUp {
  70%{
    margin-top: 3.5rem;
  }
  100%{
    margin-top: 4rem;
  }
}

@media screen and (max-width: 1023px) {
  .contentPage{
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .contentPage .contentSection h3, .contentPage p.contentText{
    width: calc(100% - 3rem);
  }

  @keyframes FlowUp {
    70%{
      margin-top: 72px;
    }
    100%{
      margin-top: 80px;
    }
  }
}

/***********游乐项目页专属样式************/
#Attraction .examples{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: 2rem 0;
}

#Attraction p.interactionHint{
  margin-top: 1rem;
}

/***********子项目页专属样式************/
#Subsystem p.interactionHint{
  margin: 0.75rem 0;
}